/* Titles
   ========================================================================= */
h1, .h1,
h2, .h2,
h3, .h3,
h4, .h4,
h5, .h5,
h6 .h6 {
	font-family: $font-family-heading;
	font-style: normal;
	font-weight: 700;
}
h1, .h1 {
	font-size: 30px;
	line-height: (35/30);
	@include media-breakpoint-up(xl) {
		font-size: 42px;
		line-height: (50/42);
	}
	@include media-breakpoint-up(xxl) {
		font-size: 48px;
		line-height: (55/48);
	}
}
h2, .h2 {
	font-size: 26px;
	line-height: (33/26);
	@include media-breakpoint-up(xl) {
		font-size: 30px;
		line-height: (35/30);
	}
	@include media-breakpoint-up(xxl) {
		font-size: 35px;
		line-height: (4/35);
	}
}
